a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:after, blockquote:before {
  content: '';
  content: none;
}

q:after, q:before {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.icon-amount {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-check {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-chevron-down {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-facebook {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-twitter {
  width: 1em;
  height: 1em;
  fill: initial;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: "Avenir LT Std", Arial, sans-serif;
  line-height: 1.6;
}

:root * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
}

html,
body {
  height: 100%;
}

.wrapper {
  position: relative;
  display: flex;
  width: 100%;
  min-height: 100vh;
  max-height: 100vh;
  font-family: "Avenir LT Std", Arial, sans-serif;
  color: #777a8c;
  background-color: #1b1c25;
}

@media only screen and (max-width: 600px) {
  .wrapper {
    overflow-x: hidden;
  }
}

a,
.icon {
  color: inherit;
  transition: all 0.2s ease;
  outline: none;
}

button {
  background-color: transparent;
  border: none;
  transition: all 0.2s ease;
  outline: none;
}

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 0 30px 25px 30px;
}

@media only screen and (max-width: 1200px) {
  .main {
    padding: 0 1% 1% 1%;
  }
}

.scroll-y {
  overflow-y: scroll;
}

.scroll-y::-webkit-scrollbar {
  width: 5px;
}

.scroll-y::-webkit-scrollbar-track {
  background-color: #16171f;
}

.scroll-y::-webkit-scrollbar-thumb {
  background-color: #2b2e3d;
}

.header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  min-height: 72px;
  padding-top: 15px;
  padding-bottom: 10px;
  border-bottom: 2px solid #1e202a;
}

.header__logo {
  max-width: 60px;
}

.header__logo img {
  width: 100%;
}

.profile {
  position: relative;
  padding-bottom: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile__open-btn {
  position: relative;
  margin-left: 10px;
}

.icon-chevron-down {
  fill: #777a8c;
}

.icon-chevron-down:hover {
  fill: #fff;
}

.profile__avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
}

.profile__dropdown {
  display: none;
  overflow: hidden;
  position: absolute;
  right: 0;
  top: 100%;
  width: 132px;
  z-index: 100;
}

.profile__dropdown .profile__open-btn {
  display: none;
}

.profile__menu {
  display: flex;
  flex-direction: column;
  line-height: 1.3;
}

.profile__menu-link {
  font-size: 14px;
  padding: 5px 10px;
  letter-spacing: 0.4px;
  border-bottom: 1px solid #393e52;
  background-color: #242734;
}

.profile__menu-link:last-child {
  border-bottom: none;
}

.profile__menu-link:hover {
  color: #eaeaea;
  background-color: #2e3243;
}

.topbar {
  min-height: 80px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 20px;
}

@media only screen and (max-width: 992px) {
  .topbar {
    min-height: 120px;
    padding-bottom: 20px;
    justify-content: center;
  }
}

.topbar__total {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  font-size: 24px;
}

@media (max-width: 1200px) {
  .info {
    font-size: calc(16px + 0.66667vw) ;
  }
}

.info .icon {
  font-size: 1em;
  fill: #bbbed0;
}

.info__number {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  font-weight: 300;
  color: #c3c6d9;
}

.info__number .icon {
  margin-right: 5px;
}

.info__caption {
  margin-top: 10px;
  text-transform: uppercase;
  font-size: 0.4em;
  font-weight: 900;
  color: #777a8c;
  white-space: nowrap;
}

.topbar__buttons {
  position: relative;
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 480px) {
  .topbar__buttons {
    justify-content: center;
    text-align: center;
  }
}

.topbar__buttons .btn {
  margin-top: 10px;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.4em 2em 0.4em;
  font-size: 14px;
  white-space: nowrap;
  letter-spacing: 0.4px;
  border-radius: 20px;
}

@media only screen and (max-width: 1200px) {
  .btn {
    padding: 0.25em 1em 0.2em;
  }
}

@media only screen and (max-width: 480px) {
  .btn {
    white-space: normal;
    padding: 0.25em 0.4em 0.2em;
  }
}

.btn .icon {
  margin-left: 0.34em;
  font-size: 1.2em;
}

.btn--transparent {
  background-color: transparent;
  margin-right: 10px;
}

.btn--transparent:hover {
  color: #fff;
}

.btn--transparent:hover .icon {
  fill: #fff;
}

.btn--green {
  margin-left: 3vw;
  color: #fff;
  background-color: #4da568;
  border: solid 1px #51ad6d;
}

@media only screen and (max-width: 1600px) {
  .btn--green {
    margin-left: 0;
  }
}

.btn--green:hover {
  background-color: #45945d;
}

.popup-stats {
  position: absolute;
  top: calc( 100% + 20px);
  right: 12px;
  width: 100%;
  display: none;
  z-index: 1000;
}

@media only screen and (max-width: 992px) {
  .popup-stats {
    right: 0;
    top: calc( 100% + 5px);
  }
}

.chevronRotate {
  transform: rotate(180deg);
}

.stats-inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 26px 15px;
  background-color: #282c3b;
  box-shadow: 0px 5px 5px 0 rgba(0, 0, 0, 0.1);
}

.stats-inner:before {
  content: "";
  position: absolute;
  top: -6px;
  left: 14%;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 6px 6px 6px;
  border-color: transparent transparent #282c3b transparent;
}

.table-wrap {
  margin-top: 20px;
  color: #777a8c;
  background-color: #242734;
}

.table {
  width: 100%;
  font-family: "Avenir LT Std", Arial, sans-serif;
}

.table thead tr {
  height: 44px;
  font-size: 12px;
  font-weight: 900;
  letter-spacing: 0.3px;
  text-align: center;
  background-color: #242734;
}

.table thead tr th {
  vertical-align: middle;
}

.table tbody tr {
  background-color: #1e202a;
}

.table tbody tr:not(:last-child) {
  border-bottom: 1px solid #242734;
}

.table tbody tr td {
  padding-top: 20px;
  padding-bottom: 20px;
  vertical-align: middle;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .table tbody tr td {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 12px;
  }
}

.table .ceil-head__players {
  width: 155px;
}

@media only screen and (max-width: 992px) {
  .table .ceil-head__players {
    width: auto;
  }
}

.table .ceil-players {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-left: 15px;
  padding-right: 15px;
}

@media only screen and (max-width: 992px) {
  .table .ceil-players {
    flex-direction: column;
  }
}

@media only screen and (max-width: 480px) {
  .table .ceil-players {
    padding-left: 1%;
    padding-right: 1%;
  }
}

.table .ceil-player {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  color: #c9496f;
  border-radius: 4px;
  background-color: transparent;
  border: solid 2px #c9496f;
}

.table .ceil-player img {
  width: 100%;
}

.table .ceil-player2 {
  color: #7e5f9d;
  border: solid 2px #7e5f9d;
}

.table .ceil-unknown {
  border-width: 1px;
}

.table .player--winner {
  position: relative;
}

.table .player--winner:after {
  content: "";
  position: absolute;
  top: -15px;
  right: 0;
  width: 12.5px;
  height: 11px;
  display: block;
  background-image: url(../img/crown.png);
  background-size: cover;
  background-repeat: no-repeat;
}

.table .sign-question {
  font-size: 25px;
  font-weight: 900;
}

.table .versus {
  margin: 5px;
  font-size: 17px;
  font-weight: 900;
  letter-spacing: 1.2px;
}

.table .ceil-head__items {
  text-align: left;
}

@media only screen and (min-width: 1500px) {
  .table .ceil-head__items {
    padding-left: 4vw;
  }
}

.table .ceil__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

@media only screen and (min-width: 1500px) {
  .table .ceil__items {
    padding-left: 4vw;
  }
}

.table .items__weapon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 45px;
  margin-right: 10px;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.1);
}

@media only screen and (max-width: 1366px) {
  .table .items__weapon {
    margin-bottom: 5px;
  }
}

@media only screen and (max-width: 768px) {
  .table .items__weapon:nth-child(n+2):not(:last-child) {
    display: none;
  }
}

@media only screen and (max-width: 380px) {
  .table .items__weapon {
    width: 36px;
    height: 30px;
  }
}

.table .items__weapon img {
  width: 54px;
  height: 45px;
  object-fit: scale-down;
}

@media only screen and (max-width: 380px) {
  .table .items__weapon img {
    width: 36px;
    height: 30px;
  }
}

.table .items__more {
  position: absolute;
  display: flex;
  top: 2px;
  right: -2px;
  font-family: inherit;
  font-size: 12px;
  letter-spacing: 0.3px;
  color: inherit;
}

.table .items__more:hover {
  cursor: pointer;
  color: #eaeaea;
}

.table .ceil-head__value {
  width: 300px;
}

@media only screen and (max-width: 1600px) {
  .table .ceil-head__value {
    width: auto;
  }
}

.table .ceil__value {
  display: flex;
  justify-content: center;
  align-items: center;
}

.table .ceil__value .icon {
  margin-right: 1px;
  fill: #777a8c;
}

.table .ceil-head__status {
  padding-left: 20px;
  padding-right: 20px;
}

@media only screen and (max-width: 992px) {
  .table .ceil-head__status {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.table .status-open {
  font-size: 14px;
  letter-spacing: 0.4px;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .table .status-open {
    font-size: 12px;
  }
}

.table .ceil__chart {
  display: flex;
  justify-content: center;
  align-items: center;
}

.table .chart__svg {
  transform: rotate(-90deg);
}

.table .chart__svg circle {
  stroke-dashoffset: 0;
  stroke: #282b38;
  stroke-width: 2px;
}

.table .chart__svg .chart__bar {
  stroke: #7e5f9d;
}

.table .chart__cont {
  display: block;
  height: 40px;
  width: 40px;
  border-radius: 100%;
  position: relative;
}

.table .chart__cont:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  content: attr(data-pct) "s";
  font-size: 14px;
  color: #7e5f9d;
}

.table .chart__cont--rose .chart__bar {
  stroke: #c9496f;
}

.table .chart__cont--rose .chart__cont:after {
  color: #c9496f;
}

.table .ceil__action {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.table .ceil__action .btn {
  margin: 7px;
}

.table .btn--rose {
  color: #c9496f;
  border: 1px solid #c9496f;
}

.table .btn--rose:hover {
  color: #fff;
  border-color: #fff;
}

.table .btn--purple {
  color: #7e5f9d;
  border: 1px solid #7e5f9d;
}

.table .btn--purple:hover {
  color: #eaeaea;
  border-color: #eaeaea;
}

.sidebar.is-open {
  right: 0;
}

.sidebar {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 385px;
  padding-right: 7px;
  background-color: #1e202a;
  transition: all 0.4s ease;
  z-index: 100;
}

@media only screen and (max-width: 600px) {
  .sidebar {
    position: absolute;
    right: -320px;
    top: 0;
    width: 320px;
  }
}

.sidebar .sidebar__online {
  padding: 15px 0;
  font-size: 14px;
  text-align: center;
  color: #fff;
  border-bottom: 1px solid #242734;
}

@media only screen and (max-height: 680px) {
  .sidebar .sidebar__online {
    padding: 5px 0;
  }
}

.sidebar .chat {
  flex-grow: 1;
  height: 50vh;
  padding: 10px 0 10px 10px;
}

.sidebar .chat__feed {
  height: calc(100% - 50px);
}

.sidebar .chat__item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 6px;
  padding-right: 10px;
}

@media only screen and (max-width: 768px) {
  .sidebar .chat__item {
    flex-direction: column;
  }
}

.sidebar .chat__photo {
  display: flex;
  margin-right: 15px;
  margin-bottom: 5px;
  background-color: #242734;
}

@media only screen and (min-width: 600px) and (max-width: 768px) {
  .sidebar .chat__photo {
    align-self: flex-end;
    margin-top: 5px;
    margin-right: 0;
  }
  .sidebar .chat__photo img {
    width: 30px;
    height: 30px;
  }
}

.sidebar .chat__photo img {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  overflow: hidden;
}

.sidebar .chat__message {
  position: relative;
  padding: 9px 10px 6px 10px;
  font-size: 14px;
  background-color: #242734;
  border-radius: 4px;
}

.sidebar .chat__message:before {
  content: "";
  position: absolute;
  left: -6px;
  top: 10px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 6px 6px 0;
  border-color: transparent #242734 transparent transparent;
}

.sidebar .message__author {
  margin-bottom: 10px;
  font-weight: 900;
  color: white;
}

.sidebar .message__text {
  line-height: 1.2;
}

.sidebar .chat__form-wrap {
  position: relative;
  margin-top: 25px;
}

@media only screen and (max-height: 680px) {
  .sidebar .chat__form-wrap {
    margin-top: 15px;
  }
}

.sidebar .chat__input {
  width: 100%;
  height: 36px;
  padding-right: 63px;
  padding-left: 10px;
  border-radius: 4.1px;
  background-color: #16171f;
  border: none;
  color: #eaeaea;
}

.sidebar .chat__send {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  padding: 5px 13px;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 900;
  color: #777a8c;
}

.sidebar .chat__send:hover {
  cursor: pointer;
  color: #eaeaea;
}

.sidebar .sidebar__poster {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 23px 5px 0 5px;
}

.sidebar .sidebar__poster img {
  width: 100%;
  border-style: solid;
  border-width: 3px;
  border-image-source: linear-gradient(to top, #ac1104, #fe801a);
  border-image-slice: 1;
}

@media only screen and (max-height: 680px) {
  .sidebar .sidebar__poster {
    display: none;
  }
}

.sidebar .sidebar__footer {
  margin-top: 10px;
  padding-bottom: 10px;
  padding-right: 7px;
}

@media only screen and (min-height: 480px) and (max-height: 640px) {
  .sidebar .sidebar__footer {
    margin-top: 15px;
  }
}

@media only screen and (max-height: 480px) {
  .sidebar .sidebar__footer {
    margin-top: 5px;
  }
}

.sidebar .footer__socials {
  display: flex;
  justify-content: flex-end;
}

.sidebar .socials__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  margin-left: 10px;
  border-radius: 50%;
  background-color: #777a8c;
}

.sidebar .socials__item .icon {
  fill: #1e202a;
  font-size: 16px;
}

.sidebar .footer__nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin-top: 20px;
}

@media only screen and (max-height: 640px) {
  .sidebar .footer__nav {
    margin-top: 10px;
  }
}

.sidebar .footer__nav-link {
  margin-left: 15px;
  margin-bottom: 5px;
  font-size: 12px;
}

.sidebar .sidebar__footer-copy {
  margin-top: 8px;
  font-size: 12px;
  font-weight: 300;
  text-align: right;
}

.modal-popup {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 20px;
  color: #777a8c;
  background-color: #1e202a;
  border-radius: 4px;
}

.modal-popup .modal__title {
  padding: 22px 25px 15px 0;
  margin: 0 20px 0 30px;
  font-size: 16px;
  font-weight: 300;
  text-align: left;
  border-bottom: 2px solid #282b38;
}

@media only screen and (max-width: 768px) {
  .modal-popup .modal__title {
    margin: 0 10px 0 10px;
    line-height: 1.2;
  }
}

.modal-popup .mfp-close {
  right: 4px;
  top: 2px;
  color: #777a8c !important;
}

#modal-history {
  min-height: 760px;
}

#modal-history thead tr {
  background-color: #1e202a;
}

#modal-history tbody tr td {
  background-color: #242734;
  padding: 15px 0;
}

@media only screen and (min-width: 768px) {
  #modal-history .table .ceil-head__players {
    width: 180px;
  }
}

@media only screen and (min-width: 768px) {
  #modal-history .table .ceil-players {
    padding-left: 35px;
  }
}

@media only screen and (min-width: 480px) and (max-width: 992px) {
  #modal-history .table .ceil-players {
    flex-direction: row;
    justify-content: flex-start;
  }
}

#modal-history .ceil__value {
  font-size: 14px;
}

#modal-history .ceil-winner {
  display: flex;
  justify-content: center;
}

#modal-history .row__data td {
  padding-top: 0;
  padding-bottom: 10px;
  border-bottom: 1px solid #1e202a;
  text-align: left;
}

#modal-history .ceil__data {
  display: flex;
